<template>
  <div id="app">
    Hi，{{ username }}。欢迎来到SignalR聊天室！
    <hr />发送给：
    <select v-model="sendTo">
      <option value="All">全部</option>
      <option v-for="user in userList" :value="user" :key="user">{{user}}</option>
    </select>
    <hr />
    <input id="text" type="text" v-model="message" />
    <button v-on:click="sendMessage">发送消息</button>
    <hr />
    <button v-on:click="clearMessageList">清空聊天记录</button>
    <hr />
    <div id="messageList" v-html="messageList">{{ messageList }}</div>
  </div>
</template>

<script>
import * as moment from "moment";
const signalR = require("@microsoft/signalr");

export default {
  name: "HelloWorld",
  data() {
    return {
      sendTo: "All",
      message: "",
      username: prompt("请输入用户名", ""),
      userList: [],
      messageList: null,
      connection: null
    };
  },
  mounted: function() {
    this.initSignalR();
  },
  methods: {
    initSignalR: function() {
      var url = `/ChatHub?UserName=${this.username}&RoomId=default`;
      this.connection = new signalR.HubConnectionBuilder().withUrl(url).build();
      this.connection.on("UserJoined", data => {
        alert(data);
        let message = this.formatMessage(
          data.EventTime,
          data.EventType,
          data.Content
        );
        this.pushMessageList(message);
        this.joinUser(data.UserID);
      });
      this.connection.on("UserLeaved", data => {
        let message = this.formatMessage(
          data.EventTime,
          data.EventType,
          data.Content
        );
        this.pushMessageList(message);
        this.leaveUser(data.UserID);
      });
      this.connection.on("ReceiveMessage", data => {
        let message = this.formatMessage(
          data.EventTime,
          data.EventSender,
          data.Content
        );
        this.pushMessageList(message);
      });
      this.connection
        .start()
        .then(() => {
          let message = this.formatMessage(
            new Date(),
            "系统消息",
            "SignalR连接成功"
          );
          this.pushMessageList(message);
        })
        .catch(err => {
          let message = this.formatMessage(
            new Date(),
            "系统消息",
            err.toString()
          );
          this.pushMessageList(message);
        });
    },

    sendMessage: function() {
      if (this.connection == null) {
        return;
      }
      if (this.message == null || this.message == "") {
        alert("不允许发送空消息");
        return;
      }
      this.connection
        .invoke("SendTo", this.sendTo, this.message, this.username)
        .then(() => {
          let message = this.formatMessage(
            new Date(),
            this.username,
            this.message
          );
          this.pushMessageList(message);
          this.message = null;
        })
        .catch(err => {
          let message = this.formatMessage(
            new Date(),
            "系统消息",
            err.tostring()
          );
          this.pushMessageList(message);
        });
    },

    formatMessage: function(sendTime, sender, message) {
      return (
        moment(sendTime).format("YYYY-MM-DD hh:mm:ss") +
        " - " +
        sender +
        " : " +
        message
      );
    },

    clearMessageList: function() {
      this.messageList = null;
    },

    pushMessageList: function(message) {
      this.messageList =
        this.messageList == null
          ? message
          : this.messageList + "<br/>" + message;
    },
    joinUser: function(userId) {
      this.userList.push(userId);
    },
    leaveUser: function(userId) {
      let index = this.userList.findIndex(x => x == userId);
      if (index != -1) {
        this.userList.splice(index, 1);
      }
    }
  }
};
</script>
